import React from "react";

const PostStyle = {
  "background-color": "white",
  "margin": "0px 0px 10px 10px",
  "box-shadow": "0px 0px 10px rgba(0, 0, 0, 0.15)",
  "border": "1px solid  #BB0A0A",
  "padding": "5px"
};

const FormStyle = {
    display: "flex",
    "flex-wrap": "wrap",
    "max-width": "65%",
    "font-size": "14pt",
    margin: "40px auto",
    padding: "10px",
    "box-shadow": "0px 0px 10px rgba(0, 0, 0, 0.15)",
    "border-radius": "10px",
    "background-color": "white",
    "vertical-align": "center",
    "border": "1px solid  #BB0A0A",
    "padding": "5px"
  };
  
  const FormStyleTitle = {
    width: "100%",
    padding: "12px 20px",
    margin: "8px 0",
    display: "inline-block",
    "background-color": "#E2E2E2",
    "border-radius": "5px",
    border: "0px",
    "box-sizing": "border-box"
  };
  
  const FormButton = {
    "background-color": " #BB0A0A",
    outline: "none",
    height: "38px",
    display: "inline-block",
    "margin-top": "100px",
    float: "none",
    "text-align": "center",
    padding: "10px",
    "font-size": "14px",
    color: "black",
    border: "2px solid  #BB0A0A",
    "border-radius": "5px"
  };

class Description extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      username: '',
      description: ''
    };
    this.handleChangeusername = this.handleChangeusername.bind(this);
    this.handleChangeDescription = this.handleChangeDescription.bind(this);

    this.UpdateUserProfile = this.UpdateUserProfile.bind(this);
    this.GetUserProfile = this.GetUserProfile.bind(this);
  }

  handleChangeusername(event) {
    this.setState({ username: event.target.value });
  }

  handleChangeDescription(event) {
    this.setState({ description: event.target.value });
  }

  GetUserProfile(event){
    event.preventDefault();
    fetch(`http://18.218.86.254:8000/fetch_profile?username=${String(this.state.username)}`, {
        headers: {
          "Content-Type": "application/json",
          },
          method: "GET"
    }).then(result => result.json())
        .then(result => {
            console.log(result)
            this.setState({
                isLoaded: true,
                description: result["description"]
              });
        })
  }

  UpdateUserProfile(event){
    event.preventDefault();
    console.log(
        JSON.stringify(
            {
                username: String(this.state.username),
                description: String(this.state.description)
            }
        )
    )
    fetch("http://18.218.86.254:8000/update_profile", {
            body: JSON.stringify(
                {
                    username: String(this.state.username),
                    description: String(this.state.description)
                }
            ),
            headers: {
              "Content-Type": "application/json",
              },
              method: "POST"
        }).then(res => res.text()).then(
            text => {
                console.log(text)
                this.setState({
                    isLoaded: false,
                    description: this.state.description
                   
                })
            }
        )
}

  render() {
    const { error, isLoaded, currentDescription } = this.state;
    if (error) {
      return <div>Error</div>;
    } else if (!isLoaded) {
      return (
      <div>
          <h3>Get User Profile</h3>
          <form  style={FormStyle}>
            Name: {this.state.username}
                <input
                type="text"
                value={this.state.username}
                onChange={this.handleChangeusername}
                style={FormStyleTitle}
                />

            <input type="submit" value="get profile" style={FormButton} onClick={this.GetUserProfile}/>
            </form>
      </div>
      );
    } else {
      return (
        <div>
         <h2>User Descriptions</h2>
            <form  style={FormStyle}>
                Name:
                <input
                type="text"
                value={this.state.username}
                onChange={this.handleChangeusername}
                style={FormStyleTitle}
                />

                Description:
                <input
                type="text"
                value={this.state.description}
                onChange={this.handleChangeDescription}
                style={FormStyleTitle}
                />

                <input type="submit" value="update profile" style={FormButton} onClick={this.UpdateUserProfile}/>
                <input type="submit" value="get profile" style={FormButton} onClick={this.GetUserProfile}/>
            </form> 
            <div>
                <p>Name:{this.state.username}</p>
                <p>Description:{this.state.description}</p>
            </div>
        </div>
      )
    }
  }
}
/*
Its a user description
The form is for looking a users feed of posts. Can filter by category....
*/

export default Description;



