import React from "react";

const Container = {
  "display": "grid",
  "grid-template-columns": "1fr 1fr",
  "grid-gap": "1rem",
  "text-align": "center",
  "margin-bottom": "2rem",
}

const FormStyle = {
  display: "flex",
  "flex-wrap": "wrap",
  "max-width": "65%",
  "font-size": "14pt",
  margin: "40px auto",
  padding: "10px",
  "box-shadow": "0px 0px 10px rgba(0, 0, 0, 0.15)",
  "border-radius": "10px",
  "background-color": "white",
  "vertical-align": "center",
  "border": "1px solid  #BB0A0A",
  "padding": "5px"
};

const FormStyleTitle = {
  width: "100%",
  padding: "12px 20px",
  margin: "8px 0",
  display: "inline-block",
  "background-color": "#E2E2E2",
  "border-radius": "5px",
  border: "0px",
  "box-sizing": "border-box"
};

const FormButton = {
  "background-color": " #BB0A0A",
  outline: "none",
  height: "38px",
  display: "inline-block",
  "margin-top": "100px",
  float: "none",
  "text-align": "center",
  padding: "10px",
  "font-size": "14px",
  color: "black",
  border: "2px solid  #BB0A0A",
  "border-radius": "5px"
};

