class CreatePost extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      username: "",
      LinkA: "",
      LinkB: ""
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleChangeusername = this.handleChangeusername.bind(this);
    this.handleChangeLinkA = this.handleChangeLinkA.bind(this);
    this.handleChangeLinkB = this.handleChangeLinkB.bind(this);
    this.uploadTopic = this.uploadTopic.bind(this)
  }

  handleChange(event) {
    this.setState({ title: event.target.value });
  }

  handleChangeusername(event) {
    this.setState({ username: event.target.value });
  }

  handleChangeLinkA(event) {
    this.setState({ LinkA: event.target.value });
  }

  handleChangeLinkB(event) {
    this.setState({ LinkB: event.target.value });
  }

  uploadTopic(event){
    event.preventDefault();
    console.log(JSON.stringify(`{title: ${this.state.title}, username: ${this.state.username}, links: [${this.state.LinkA}, ${this.state.LinkB}]}`))
    console.log(`{title: ${this.state.title}, username: ${this.state.username}, links: [${this.state.LinkA}, ${this.state.LinkB}]}`)
    //JSON.stringify(`{"title": "Dr. Sleep", "username": "jing", "links": ["https://www.theringer.com/movies/2019/11/8/20954144/doctor-sleep-review"]}`)
    
    fetch("http://18.218.86.254:8000/create_post", {
      body: JSON.stringify({
        title: String(this.state.title), 
        username: String(this.state.username), 
        links: [String(this.state.LinkA), String(this.state.LinkB)]}),
      headers: {
        "Content-Type": "application/json",
        },
        method: "POST"
    }).then(res => res.text()).then(
      text => {
        console.log(text)
      }
    )
    
  }

  render() {
    return (
      <div style={Container}>
      <div>
          <form style={FormStyle}>
              title
              <input
                type="text"
                value={this.state.title}
                onChange={this.handleChange}
                style={FormStyleTitle}
              />
              username
              <input
                type="text"
                value={this.state.username}
                onChange={this.handleChangeusername}
                style={FormStyleTitle}
              />
          </form>
        </div>
        <div>
        <form style={FormStyle}>
          external links
          <input
            type="text"
            value={this.state.LinkA}
            onChange={this.handleChangeLinkA}
            style={FormStyleTitle}
          />
          <input
            type="text"
            value={this.state.LinkB}
            onChange={this.handleChangeLinkB}
            style={FormStyleTitle}
          />

          <input type="submit" value="submit" style={FormButton} onClick={this.uploadTopic}/>
         </form> 
        </div>
      </div>
    );
  }
}

export default CreatePost;



