import React from "react";
import { Link } from "react-router-dom";

// The Header creates links that can be used to navigate
// between routes.

const headerStyle = {
  "background-color": "#BB0A0A",
  "text-align": "center",
  "font-size": "1.2rem",
  "padding": "5px"
};

const itemStyle = {
  "font-weight": "bold",
  color: "ghostwhite",
  margin: "0px 10px 0px 10px"
};

const Header = () => (
  <header style={headerStyle}>
    <Link to="/" style={itemStyle}>
      home
    </Link>

    <Link to="/createpost" style={itemStyle}>
      create post
    </Link>

    <Link to="/login" style={itemStyle}>
      login
    </Link>

    <Link to="/description" style={itemStyle}>
      description
    </Link>
  </header>
);

export default Header;



