import React from "react";

const Container = {
    "display": "grid",
    "grid-template-columns": "1fr 1fr",
    "grid-gap": "1rem",
    "text-align": "center",
    "margin-bottom": "2rem",
}

const FormStyle = {
    display: "flex",
    "flex-wrap": "wrap",
    "max-width": "65%",
    "font-size": "14pt",
    margin: "40px auto",
    padding: "10px",
    "box-shadow": "0px 0px 10px rgba(0, 0, 0, 0.15)",
    "border-radius": "10px",
    "background-color": "white",
    "vertical-align": "center",
    "border": "1px solid  #BB0A0A",
    "padding": "5px"
  };
  
  const FormStyleTitle = {
    width: "100%",
    padding: "12px 20px",
    margin: "8px 0",
    display: "inline-block",
    "background-color": "#E2E2E2",
    "border-radius": "5px",
    border: "0px",
    "box-sizing": "border-box"
  };
  
  const FormButton = {
    "background-color": " #BB0A0A",
    outline: "none",
    height: "38px",
    display: "inline-block",
    "margin-top": "100px",
    float: "none",
    "text-align": "center",
    padding: "10px",
    "font-size": "14px",
    color: "black",
    border: "2px solid  #BB0A0A",
    "border-radius": "5px"
  };

class LogMenu extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            LoggedIn: false,
            email: "",
            password: ""
        };

        this.LoginRequest = this.LoginRequest.bind(this);
        this.handleEmail = this.handleEmail.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
    }

    LoginRequest(event){
        event.preventDefault();
        console.log( `{Email: ${this.state.email}, Password: ${this.state.password}}`)
    }

    handleEmail(event) {
        this.setState({ email: event.target.value });

    }
    handlePassword(event) {
        this.setState({ password: event.target.value });
    }

    render(){
        return (
            <div style={Container}>
                <div>
                    <h2>new users</h2>
                    <form style={FormStyle}>
                        <input
                        type="text"
                        value={this.state.title}
                        onChange={this.handleChange}
                        style={FormStyleTitle}
                        placeholder="name"
                        />

                        <input
                        type="text"
                        value={this.state.text}
                        onChange={this.handleChangeText}
                        style={FormStyleTitle}
                        placeholder="email"
                        />

                        <input
                        type="text"
                        value={this.state.text}
                        onChange={this.handleChangeText}
                        style={FormStyleTitle}
                        placeholder="password"
                        />
                        <input type="submit" value="submit" style={FormButton} />
                    </form>
                </div>
                <div>
                    <h2>returning users</h2>
                    <form style={FormStyle}>
                        <input
                        type="text"
                        value={this.state.email}
                        onChange={this.handleEmail}
                        style={FormStyleTitle}
                        />

                        <input
                        type="text"
                        value={this.state.password}
                        onChange={this.handlePassword}
                        style={FormStyleTitle}
                        />
                        <input type="submit" value="submit" style={FormButton} onClick={this.LoginRequest}/>
                    </form>
                </div>
            </div>
        )
    }
}

export default LogMenu


